:root{--primaryColor: #e03e0e;--primaryColor2: #fa8717;--primaryColor3: #fee6cf;--bannerColor: #ea5023;--fontFamily: "GothamRnd", sans-serif;--templateColor: #e03e0e;--templateColor2: #ff8a73;--whiteColor: #ffffff;--lightColor: #f8f8fc;--darkColor: #7c7c80;--blackColor: #000000;--paragraphColor: #818992;--transition: 0.5s;--darkColor1: #474747;--blue: #4a50ff;--blueActive: #7d81f4;--pink: #d74a96;--pinkHover: #f076b8;--lightBlue: #6195fd;--lightBlueHover: #1763fb;--mint: #24b6b1;--mintHover: #2c9491;--green: #47821c;--greenHover: #5bb01c;--purpule: #7b70ff;--purpleHover: #5145e2;--yellow: #ffc736;--yellowHover: #fbb911;--breadcrumbtextcolor: #d1c4e9;--breadcrumbtextcoloractive: #fff;--lightOrange: #ffc61a;--orange1: #f16c1c}.breadcrumb-wrapper{box-sizing:border-box;padding:3rem 0;display:flex;flex:1;justify-content:space-between;align-items:center}.breadcrumb-wrapper .page__section{color:var(--darkColor);border-radius:2px;display:flex;flex:1;align-items:center}.breadcrumb-wrapper .page__section:nth-of-type(n+2){margin-top:2.5rem}.breadcrumb-wrapper .page__section .breadcrumb{display:flex;flex:1;align-items:center;margin-bottom:0}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list{display:flex;flex-wrap:wrap;gap:.75rem;margin-bottom:0}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group{display:inline-flex;align-items:center}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group a.breadcrumb__point{font-style:normal;font-weight:normal;font-size:20px;line-height:140%;color:#7C7C80}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group .breadcrumb__divider{margin-left:.75rem;font-size:20px;color:#C4C4C4}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group span.breadcrumb__point{font-style:normal;font-weight:bold;font-size:20px;line-height:140%;color:var(--primaryColor)}
